/* Skills.css */
.skills-section {
  background-color: #f9f9f9;
  padding: 50px 20px;
  text-align: center;
}

.skills-section h2 {
  color: #28a745; /* Verde */
}

.skills-section ul {
  list-style-type: none;
  padding: 0;
}

.skills-section li {
  display: inline-block;
  margin: 10px 20px;
  font-size: 1.2em;
  color: #ff8c00; /* Arancione */
}
