/* Projects.css */
.projects-section {
  background-color: #fff;
  padding: 50px 20px;
}

.projects-section h2 {
  color: #28a745; /* Verde */
  text-align: center;
}

.projects-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.project-card {
  background-color: #f0f8ff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin: 20px;
  width: calc(33% - 40px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.project-card h3 {
  color: #ff8c00; /* Arancione */
}

.project-card p {
  color: #333;
}
