/* Contact.css */
.contact-section {
  background-color: #f9f9f9;
  padding: 50px 20px;
  text-align: center;
}

.contact-section h2 {
  color: #28a745; /* Verde */
}

.contact-section form {
  max-width: 600px;
  margin: 0 auto;
}

.contact-section input,
.contact-section textarea {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.contact-section button {
  background-color: #28a745; /* Verde */
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.contact-section button:hover {
  background-color: #ff8c00; /* Arancione */
}
